$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Button
.wp-block-btn-button {
	* {
		box-sizing: border-box;
	}

	.btnButton {
		display: inline-flex;
		align-items: center;
		gap: 12px;
		justify-content: center;
		text-decoration: none;
		transition: all .3s ease-in-out;

		img {
			width: 1em;
			height: 1em;
			object-fit: contain;
		}

		i {
			font-size: inherit;
			font-style: inherit;
			color: inherit;
		}
	}

	/* START TOOLTIP STYLES */
	[tooltip] {
		position: relative;
		/* opinion 1 */
	}

	/* Applies to all tooltips */
	[tooltip]::before,
	[tooltip]::after {
		text-transform: none;
		/* opinion 2 */
		font-size: .9em;
		/* opinion 3 */
		line-height: 1;
		user-select: none;
		pointer-events: none;
		position: absolute;
		display: none;
		opacity: 0;
	}

	[tooltip]::before {
		content: '';
		border: 5px solid transparent;
		/* opinion 4 */
		z-index: 1001;
		/* absurdity 1 */
	}

	[tooltip]::after {
		content: attr(tooltip);
		/* magic! */

		/* most of the rest of this is opinion */
		font-family: Helvetica, sans-serif;
		text-align: center;

		/* 
			Let the content set the size of the tooltips 
			but this will also keep them from being obnoxious
		*/
		min-width: 3em;
		max-width: 21em;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		padding: 1ch 1.5ch;
		border-radius: .3ch;
		box-shadow: 0 1em 2em -.5em rgba(0, 0, 0, 0.35);
		background: #333;
		color: #fff;
		z-index: 1000;
		/* absurdity 2 */
	}

	/* Make the tooltips respond to hover */
	[tooltip]:hover::before,
	[tooltip]:hover::after {
		display: block;
	}

	/* Don't show empty tooltips */
	[tooltip='']::before,
	[tooltip='']::after {
		display: none !important;
	}

	/* POSITION: TOP */
	[tooltip]:not([position])::before,
	[tooltip][position^='top']::before {
		bottom: 100%;
		border-bottom-width: 0;
		border-top-color: #333;
	}

	[tooltip]:not([position])::after,
	[tooltip][position^='top']::after {
		bottom: calc(100% + 5px);
	}

	[tooltip]:not([position])::before,
	[tooltip]:not([position])::after,
	[tooltip][position^='top']::before,
	[tooltip][position^='top']::after {
		left: 50%;
		transform: translate(-50%, -.5em);
	}

	/* POSITION: BOTTOM */
	[tooltip][position^='bottom']::before {
		top: 100%;
		border-top-width: 0;
		border-bottom-color: #333;
	}

	[tooltip][position^='bottom']::after {
		top: calc(100% + 5px);
	}

	[tooltip][position^='bottom']::before,
	[tooltip][position^='bottom']::after {
		left: 50%;
		transform: translate(-50%, .5em);
	}

	/* POSITION: LEFT */
	[tooltip][position^='left']::before {
		top: 50%;
		border-right-width: 0;
		border-left-color: #333;
		left: calc(0em - 5px);
		transform: translate(-.5em, -50%);
	}

	[tooltip][position^='left']::after {
		top: 50%;
		right: calc(100% + 5px);
		transform: translate(-.5em, -50%);
	}

	/* POSITION: RIGHT */
	[tooltip][position^='right']::before {
		top: 50%;
		border-left-width: 0;
		border-right-color: #333;
		right: calc(0em - 5px);
		transform: translate(.5em, -50%);
	}

	[tooltip][position^='right']::after {
		top: 50%;
		left: calc(100% + 5px);
		transform: translate(.5em, -50%);
	}

	/* KEYFRAMES */
	@keyframes bntTooltipsVertical {
		to {
			opacity: .9;
			transform: translate(-50%, 0);
		}
	}

	@keyframes btnTooltipsHorizontal {
		to {
			opacity: .9;
			transform: translate(0, -50%);
		}
	}

	/* FX All The Things */
	[tooltip]:not([position]):hover::before,
	[tooltip]:not([position]):hover::after,
	[tooltip][position^='top']:hover::before,
	[tooltip][position^='top']:hover::after,
	[tooltip][position^='bottom']:hover::before,
	[tooltip][position^='bottom']:hover::after {
		animation: bntTooltipsVertical 300ms ease-out forwards;
	}

	[tooltip][position^='left']:hover::before,
	[tooltip][position^='left']:hover::after,
	[tooltip][position^='right']:hover::before,
	[tooltip][position^='right']:hover::after {
		animation: btnTooltipsHorizontal 300ms ease-out forwards;
	}
}